/* Стили для навигационного меню */
nav {
    background-color: #f2f2f2;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  li {
    display: inline-block;
    margin: 10px;
  }
  
  a {
    display: inline-block;
    padding: 10px;
    text-decoration: none;
    color: #333;
    font-weight: bold;
  }
  
  a:hover {
    background-color: #ddd;
  }
  
  a:active {
    background-color: #bbb;
  }