.actlist {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    
  }
  
  .actlist li {
    margin-bottom: 10px;
  }
  
  .actlist li a {
    display: inline-block;
    position: relative;
    padding: 5px 10px;
    color: #000; /* Цвет ссылки акта */
    text-decoration: none;
    border: 1px solid #ccc; /* Граница тега a */
    border-radius: 5px;
    width: 100%; /* Растягивание по ширине */
    box-sizing: border-box;
  }
  
