.button {
    display: inline-block;
    background-color: #604caf; /* Цвет фона */
    color: white; /* Цвет текста */
    text-align: center;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  /* Эффект при наведении курсора */
  .button:hover {
    background-color: #4c45a0;
  }
  
  /* Эффект при активации кнопки */
  .button:active {
    background-color: #4a3e8e;
  }
  
  /* Стиль для отключенной кнопки */
  .button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }