.modal{
    
    width: 210vh;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    transition: 0.6s;
    pointer-events: none;
    justify-content: center;
    align-items: center;
  
}
.modal.active{
    opacity: 1;
    pointer-events: all;
}
